<template lang="pug">
  .news.inner-page
    .inner-page__main
      .news__add
        news-add(user)
      .news__list(v-if="getInfo")
        news-block(
          v-for="feed in getFeeds"
          :key="feed.id"
          :info="feed"
          :edit="getInfo.id === feed.author.id"
          :deleted="getInfo.id === feed.author.id"
        )
      button.btn(@click="loadPosts" v-if="getFeeds.length !== getAllFeeds") 
    .inner-page__aside
      friends-possible
 </template>

<script>
import { mapGetters, mapActions } from 'vuex'
import FriendsPossible from '@/components/Friends/Possible'
import FriendsRequest from '@/components/Friends/Request'
import NewsBlock from '@/components/News/Block'
import NewsAdd from '@/components/News/Add'
export default {
  name: 'News',
  components: { FriendsPossible, FriendsRequest, NewsBlock, NewsAdd },
  computed: {
    ...mapGetters('profile/feeds', ['getFeeds', 'getAllFeeds']),
    ...mapGetters('profile/info', ['getInfo'])
  },
  data: () => {
    return {
      offset: 0,
      itemPerPage: 20
    }
  },
  methods: {
    ...mapActions('profile/feeds', ['apiFeeds', 'resetFeeds']),
    loadPosts() {
      this.offset += this.itemPerPage;
      this.apiFeeds({offset: this.offset, itemPerPage: this.itemPerPage})
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.apiFeeds()
    })
  },
  created() {
    this.resetFeeds([])
  }
}
</script>

<style lang="stylus">
@import '../../assets/stylus/base/vars.styl';

.news__add {
  margin-bottom: 30px;
}
</style>



