import axios from 'axios'
import router from '@/router'

export default {
  namespaced: true,
  state: {
    searchText: '',
    tabs: [{
      id: 'all',
      text: ''
    }, {
      id: 'users',
      text: ''
    }, {
      id: 'news',
      text: ''
    }],
    tabSelect: 'all',
    result: {
      users: [],
      allUsers: null,
      news: [],
      allNews: null,

    },
    status: '',
  },
  getters: {
    searchText: s => s.searchText,
    tabs: s => s.tabs,
    tabSelect: s => s.tabSelect,
    getResult: s => s.result,
    getResultById: s => id => s.result[id],
    getStatus: s => s.status
  },
  mutations: {
    setSearchText: (s, value) => s.searchText = value,
    setTabSelect: (s, id) => s.tabSelect = id,
    routePushWithQuery(state, id) {
      let query = {}
      if (id !== 'all') query.tab = id
      if (state.searchText.length > 0) query.text = state.searchText
      router.push({
        name: 'Search',
        query
      })
    },
    setResult: (s, result) => {
      s.result[result.id] = [...new Set([...s.result[result.id], ...result.value])]
      // console.log('50',s.result[result.id])
      s.result[result.type] = result.total;
    },
    setResetResult: (s, result) => {
      s.result[result.id] = result.value;
    }
  },
  actions: {
    clearSearch({
      commit
    }) {
      commit('setSearchText', '')
      commit('setResetResult', {
        id: 'users',
        value: []
      })
      commit('setResetResult', {
        id: 'news',
        value: []
      })
    },
    resetSearch({
      commit
    }) {
      commit('setResetResult', {
        id: 'users',
        value: []
      })
      commit('setResetResult', {
        id: 'news',
        value: []
      })
    },
    changeTab({
      commit
    }, id) {
      commit('setTabSelect', id)
      commit('routePushWithQuery', id)
    },
    async searchUsers({
      commit
    }, payload) {
      let query = []
      payload && Object.keys(payload).map(el => {
        payload[el] && query.push(`${el}=${payload[el]}`)
      })
      //console.log('daa', payload, payload.offset, payload.itemPerPage)
      await axios({
        url: `users/search?${query.join('&')}`,
        params: {
          offset: payload.offset,
          itemPerPage: payload.itemPerPage
        },
        method: 'GET'
      })
        .then(response => {
          // console.log("TCL: searchUsers -> response", response.data.data)
          commit('setResult', {
            id: 'users',
            type: 'allUsers',
            value: response.data.data,
            total: response.data.total
          })
        })
        
        .catch(error => {})
    },
    async searchNews({
      commit
    }, payload) {

      if (!payload || !payload.text) {
        // alert('   ')
        return false
      }
      let query = []
      payload && Object.keys(payload).map(el => {
        payload[el] && query.push(`${el}=${payload[el]}`)
      })

      await axios({
        url: `post?${query.join('&')}`,
        params: {
          offset: payload.offset,
          itemPerPage: payload.itemPerPage
        },
        method: 'GET'
      })
        .then(response => {
          // console.log('TCL: searchNews -> response', response.data.data)
          commit('setResult', {
            id: 'news',
            type: 'allNews',
            value: response.data.data,
            total: response.data.total
          })
          console.log(payload)
        })
        .catch(error => {})
    },
    async searchAll({
      dispatch,
      commit
    }, {text, offset, itemPerPage}) {
      commit('setSearchText', text)
      await dispatch('searchUsers', {
        first_name: text,
        offset,
        itemPerPage
      })
      await dispatch('searchNews', {
        text,
        offset,
        itemPerPage
      })
    }
  }
}



