import axios from 'axios'
import moment from 'moment'
import router from '@/router'

export default {
  namespaced: true,
  state: {
    info: null,
    allActivePosts: null,
    allQueuedPosts: null,
    wall: [],
    queuedWall: [],
    users: null
  },
  getters: {
    getInfo(state) {
      if (!state.info) return
      let result = {
        ...state.info
      }
      //      ( ,      )
      //  - petrovich
      result.fullName = result.first_name + ' ' + result.last_name
      result.ages = moment().diff(result.birth_date, 'years')
      result.is_onlined = moment().diff(moment(result.last_online_time), 'seconds') <= 60
      return result
    },
    getUsersInfo(state) {
      if (!state.users) return
      let result = {
        ...state.users
      }
      //      ( ,      )
      //  - petrovich
      result.fullName = result.first_name + ' ' + result.last_name
      result.ages = moment().diff(result.birth_date, 'years')
      result.is_onlined = moment().diff(moment(result.last_online_time), 'seconds') <= 60
      return result
    },
    getWall: s => s.wall,
    getQueuedWall: s => s.queuedWall,
    // getWallPostedLength: s => s.wall.filter(el => el.type === 'POSTED').length,
    // getWallQueuedLength: s => s.wall.filter(el => el.type === 'QUEUED').length,
    getAllActivePosts: s => s.allActivePosts,
    getAllQueuedPosts: s => s.allQueuedPosts
  },
  mutations: {
    setResetWall: (s, wall) => {
      s.wall = wall
      s.queuedWall = wall
    },

    setInfo: (s, info) => (s.info = info),

    setAllWall: (s, data) => {
      s.queuedWall = data.queued.data;
      s.allQueuedPosts = data.queued.total;
    },
    setWall: (s, wall) => (s.wall = wall),
    setQueuedWall: (s, queuedWall) => (s.queuedWall = queuedWall),

    setWallById: (s, payload) => (s.wall[s.wall.indexOf(s.wall.find(el => el.id === payload.id))] = payload.value),
    setCommentsById: (s, payload) => {
      s.wall[s.wall.indexOf(s.wall.find(el => el.id === payload.post_id))].comments = payload.value
      s.wall.push('dog-nail')
      s.wall.splice(-1, 1)
    },
    setAllActivePosts: (s, allActivePosts) => (s.allActivePosts = allActivePosts),
    setAllQueuedPosts: (s, allQueuedPosts) => (s.allQueuedPosts = allQueuedPosts),
    setUsersInfo: (s, info) => (s.users = info)
  },
  actions: {
    async apiInfo({ commit }, id) {
      await axios({
        url: `users/${id}`,
        method: 'GET'
      })
        .then(response => {
          commit('setInfo', response.data.data)
        })
        .catch(error => {})
    },
    async apiWall({ commit }, { id, offset, itemPerPage }) {
      // console.log(id, offset)
      offset = offset ? '?offset=' + offset : '?offset=0'
      itemPerPage = itemPerPage ? '&itemPerPage=' + itemPerPage : ''
      console.log('fetch wall', id)
      await axios({
        url: `users/${id}/wall${offset}${itemPerPage}`,
        method: 'GET'
      })
        .then(response => {
          commit('setAllActivePosts', response.data.posted.total)
          commit('setWall', response.data.posted.data)
          commit('setAllWall', response.data)
          // commit('setQueuedWall', response.data.queued.data)
          // commit('setAllQueuedPosts', response.data.queued.total)
        })
        .catch(error => {})
    },
    async apiQueuedWall({ commit }, { id, offset, itemPerPage }) {
      // console.log(id, offset)
      offset = offset ? '?offset=' + offset : '?offset=0'
      itemPerPage = itemPerPage ? '&itemPerPage=' + itemPerPage : ''
      console.log('fetch wall', id)
      await axios({
        url: `users/${id}/wall${offset}${itemPerPage}`,
        method: 'GET'
      })
        .then(response => {
          commit('setQueuedWall', response.data.queued.data)
          commit('setAllQueuedPosts', response.data.queued.total)
        })
        .catch(error => {})
    },
    async apiWallById({ commit }, id) {
      await axios({
        url: `post/${id}`,
        method: 'GET'
      })
        .then(response => {
          commit('setWallById', {
            id,
            value: response.data.data
          })
        })
        .catch(error => {})
    },
    async apiCommentsById({ commit }, id) {
      await axios({
        url: `post/${id}/comments`,
        method: 'GET'
      })
        .then(response => {
          commit('setCommentsById', response.data.data)
        })
        .catch(error => {})
    },
    async userInfoId({ commit, dispatch }, { id, offset }) {
      console.log(id, offset)
      await axios({
        url: `users/${id}`,
        method: 'GET'
      })
        .then(async response => {
          await dispatch('apiWall', { id, offset })
          commit('setUsersInfo', response.data.data)
        })
        .catch(error => {})
    }
  }
}



