import axios from 'axios'

export default {
  namespaced: true,
  state: {
    notifications: []
  },
  getters: {
    getNotifications: s => s.notifications,
    getNotificationsLength: s => s.notifications.length,
    getNotificationsTextType: s => type => {
      switch (type) {
        case 'POST':
          return '  '
        case 'POST_COMMENT':
          return ' '
        case 'COMMENT_COMMENT':
          return '   '
        case 'FRIEND_REQUEST':
          return '    '
        case 'FRIEND_BIRTHDAY':
          return ' '
        case 'MESSAGE':
          return ' '
      }
    }
  },
  mutations: {
    setNotifications: (s, value) => (s.notifications = value)
  },
  actions: {
    async apiNotifications({ state, commit, dispatch }) {
      await axios({
        url: 'notifications',
        method: 'GET'
      })
        .then(response => {
          if (`${response.data.data.map(z => z.sent_time)}` !== `${state.notifications.map(z => z.sent_time)}`) {
            const result = response.data.data.map(el => {
              if (!el.entity_author) el.entity_author = {}
              if (!el.entity_author.photo) el.entity_author.photo = '../static/img/user/default_avatar.svg'
              if (!el.entity_author.first_name) el.entity_author.first_name = `   ID: ${el.id}`
              if (!el.entity_author.last_name) el.entity_author.last_name = `   ID: ${el.id}`
              if (!el.event_type) el.event_type = 'POST'
              return el
            })
            commit('setNotifications', result)
          }
          //    
          // setTimeout(() => {
          //   dispatch('apiNotifications')
          // }, 5000)
        })
        .catch(() => {})
    },
    // async readNotifications() {
    //   await axios({
    //     url: 'notifications?all=true',
    //     method: 'PUT'
    //   })
    //     .then(response => {})
    //     .catch(() => {})
    // }
    async readNotifications({ state, commit, dispatch }, notificationId) {
      if (!notificationId) {
        await axios({
          url: 'notifications?all=true',
          method: 'PUT'
        })
          .then(response => {})
          .catch(() => {})
      } else {
        await axios({
          url: `notifications?id=${notificationId}`,
          method: 'PUT'
        })
          .then(response => {})
          .catch(() => {})
      }
    }
  }
}



